body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background-color: rgb(40, 55, 77);
  display: flex;
  justify-content: center;
}

main {
  background-color: rgb(223, 228, 236);
  padding: 30px;
  margin-top: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, .5);
  width: 90vw;
  max-width: 400px;
  position: relative;
}

h1 {
  margin: 0 0 30px;
  text-align: center;
  color: rgb(40, 55, 77);
}

select {
  min-width: 100%;
  min-height: 40px;
  font-size: 18px;
  padding: 0 20px 0 10px;
  cursor: pointer;
  border-radius: 8px;
  border: 1px solid #c0c0c0;
}

select:focus {
  outline: none;
}

select>option:first-child {
  color: #999;
}

.list {
  min-width: 100%;
  margin-top: 20px;
  border-collapse: collapse;
}

#refresh {
  position: absolute;
  top: 8px;
  right: 10px;
  font-size: 14px;
  border: 1px solid #c0c0c0;
  padding: 2px 6px;
  border-radius: 6px;
  background-color: #f5f7fb;
  color: #505050;
  cursor: pointer;
}